// Grid system
//
// Generate semantic grid columns with these mixins.

@mixin make-row(Rsgutter: Rsgrid-gutter-width) {
  --#{Rsvariable-prefix}gutter-x: #{Rsgutter};
  --#{Rsvariable-prefix}gutter-y: 0;
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(var(--#{Rsvariable-prefix}gutter-y) * -1); // stylelint-disable-line function-disallowed-list
  margin-right: calc(var(--#{Rsvariable-prefix}gutter-x) * -.5); // stylelint-disable-line function-disallowed-list
  margin-left: calc(var(--#{Rsvariable-prefix}gutter-x) * -.5); // stylelint-disable-line function-disallowed-list
}

@mixin make-col-ready(Rsgutter: Rsgrid-gutter-width) {
  // Add box sizing if only the grid is loaded
  box-sizing: if(variable-exists(include-column-box-sizing) and Rsinclude-column-box-sizing, border-box, null);
  // Prevent columns from becoming too narrow when at smaller grid tiers by
  // always setting `width: 100%;`. This works because we set the width
  // later on to override this initial width.
  flex-shrink: 0;
  width: 100%;
  max-width: 100%; // Prevent `.col-auto`, `.col` (& responsive variants) from breaking out the grid
  padding-right: calc(var(--#{Rsvariable-prefix}gutter-x) * .5); // stylelint-disable-line function-disallowed-list
  padding-left: calc(var(--#{Rsvariable-prefix}gutter-x) * .5); // stylelint-disable-line function-disallowed-list
  margin-top: var(--#{Rsvariable-prefix}gutter-y);
}

@mixin make-col(Rssize: false, Rscolumns: Rsgrid-columns) {
  @if Rssize {
    flex: 0 0 auto;
    width: percentage(divide(Rssize, Rscolumns));

  } @else {
    flex: 1 1 0;
    max-width: 100%;
  }
}

@mixin make-col-auto() {
  flex: 0 0 auto;
  width: auto;
}

@mixin make-col-offset(Rssize, Rscolumns: Rsgrid-columns) {
  Rsnum: divide(Rssize, Rscolumns);
  margin-left: if(Rsnum == 0, 0, percentage(Rsnum));
}

// Row columns
//
// Specify on a parent element(e.g., .row) to force immediate children into NN
// numberof columns. Supports wrapping to new lines, but does not do a Masonry
// style grid.
@mixin row-cols(Rscount) {
  > * {
    flex: 0 0 auto;
    width: divide(100%, Rscount);
  }
}

// Framework grid generation
//
// Used only by Bootstrap to generate the correct number of grid classes given
// any value of `Rsgrid-columns`.

@mixin make-grid-columns(Rscolumns: Rsgrid-columns, Rsgutter: Rsgrid-gutter-width, Rsbreakpoints: Rsgrid-breakpoints) {
  @each Rsbreakpoint in map-keys(Rsbreakpoints) {
    // .row-cols defaults must all appear before .col overrides so they can be overridden.
    Rsinfix: breakpoint-infix(Rsbreakpoint, Rsbreakpoints);
    @include media-breakpoint-up(Rsbreakpoint, Rsbreakpoints) {
      // Provide basic `.col-{bp}` classes for equal-width flexbox columns
      .col#{Rsinfix} {
        flex: 1 0 0%; // Flexbugs #4: https://github.com/philipwalton/flexbugs#flexbug-4
      }

      .row-cols#{Rsinfix}-auto > * {
        @include make-col-auto();
      }

      @if Rsgrid-row-columns > 0 {
        @for Rsi from 1 through Rsgrid-row-columns {
          .row-cols#{Rsinfix}-#{Rsi} {
            @include row-cols(Rsi);
          }
        }
      }
    }
  }

  @each Rsbreakpoint in map-keys(Rsbreakpoints) {
    Rsinfix: breakpoint-infix(Rsbreakpoint, Rsbreakpoints);

    @include media-breakpoint-up(Rsbreakpoint, Rsbreakpoints) {
      .col#{Rsinfix}-auto {
        @include make-col-auto();
      }

      @if Rscolumns > 0 {
        @for Rsi from 1 through Rscolumns {
          .col#{Rsinfix}-#{Rsi} {
            @include make-col(Rsi, Rscolumns);
          }
        }

        // `Rscolumns - 1` because offsetting by the width of an entire row isn't possible
        @for Rsi from 0 through (Rscolumns - 1) {
          @if not (Rsinfix == "" and Rsi == 0) { // Avoid emitting useless .offset-0
            .offset#{Rsinfix}-#{Rsi} {
              @include make-col-offset(Rsi, Rscolumns);
            }
          }
        }
      }

      // Gutters
      //
      // Make use of `.g-*`, `.gx-*` or `.gy-*` utilities to change spacing between the columns.
      @each Rskey, Rsvalue in Rsgutters {
        .g#{Rsinfix}-#{Rskey},
        .gx#{Rsinfix}-#{Rskey} {
          --#{Rsvariable-prefix}gutter-x: #{Rsvalue};
        }

        .g#{Rsinfix}-#{Rskey},
        .gy#{Rsinfix}-#{Rskey} {
          --#{Rsvariable-prefix}gutter-y: #{Rsvalue};
        }
      }
    }
  }
}
