// Container widths
//
// Set the container width, and override it for fixed navbars in media queries.

@if Rsenable-grid-classes {
  // Single container class with breakpoint max-widths
  .container,
  // 100% wide container at all breakpoints
  .container-fluid {
    @include make-container();
  }

  // Responsive containers that are 100% wide until a breakpoint
  @each Rsbreakpoint, Rscontainer-max-width in Rscontainer-max-widths {
    .container-#{Rsbreakpoint} {
      @extend .container-fluid;
    }

    @include media-breakpoint-up(Rsbreakpoint, Rsgrid-breakpoints) {
      %responsive-container-#{Rsbreakpoint} {
        max-width: Rscontainer-max-width;
      }

      // Extend each breakpoint which is smaller or equal to the current breakpoint
      Rsextend-breakpoint: true;

      @each Rsname, Rswidth in Rsgrid-breakpoints {
        @if (Rsextend-breakpoint) {
          .container#{breakpoint-infix(Rsname, Rsgrid-breakpoints)} {
            @extend %responsive-container-#{Rsbreakpoint};
          }

          // Once the current breakpoint is reached, stop extending
          @if (Rsbreakpoint == Rsname) {
            Rsextend-breakpoint: false;
          }
        }
      }
    }
  }
}
